import { AccountService } from '@core/services/account/account.service';
import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { GlobalPositionService } from '@core/services/global-position/global-position.service';
import { HeaderService } from '@core/services/header/header.service';
import { findAccount, findDeposit } from '@core/utils';
import { zip } from 'rxjs';
import { map } from 'rxjs/operators';
import { Banners, BANNERS } from '@core/constants/banners.constants';

@Component({
  selector: 'app-global-position',
  templateUrl: './global-position.component.html',
  styles: [],
})
export class GlobalPositionComponent implements OnInit, OnDestroy {
  readonly state$ = this.createState();
  readonly pendingHirings$ = this.globalPositionService
    .pendingHirings()
    .pipe(map((response) => response.data?.[0]));

  constructor(
    private globalPositionService: GlobalPositionService,
    private accountService: AccountService,
    private headerService: HeaderService,
    @Inject(BANNERS) public readonly banners: Banners
  ) {}

  ngOnInit() {
    this.globalPositionService.fetchGlobalPosition();
    this.headerService.updateTitle(null);
  }

  ngOnDestroy() {
    this.globalPositionService.destroy();
  }

  private createState() {
    return zip(
      this.globalPositionService.globalPosition$,
      this.globalPositionService.globalPosition$.pipe(findAccount()),
      this.globalPositionService.globalPosition$.pipe(findDeposit())
    ).pipe(map(([globalPosition, account, deposit]) => ({ globalPosition, account, deposit })));
  }
}
