import { Component, OnInit, Input } from '@angular/core';
import { FinancialAccount } from '@api/models';
import { RouterMenu } from '@core/config/routing-menu';
import { isDeposit, isAccount } from '@core/utils';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styles: [],
})
export class ProductItemComponent implements OnInit {
  @Input() product: FinancialAccount;

  get isDeposit() {
    return isDeposit(this.product);
  }

  get isAccount() {
    return isAccount(this.product);
  }

  get productDetailPath() {
    if (this.isAccount) {
      return `./../../${RouterMenu.TRANSACTIONS}`;
    } else if (this.isDeposit) {
      return `./../../${RouterMenu.DEPOSIT}`;
    } else {
      return '';
    }
  }

  ngOnInit() {}
}
